import common from '../../../common/globalVariable.js'
import Request from '../../../util/luch-request/request.js'
export default{
	data(){
		return {
			title:'',
			account:'',
			F:'',
			T:'',
			I:'',
			L:'',
			W:'',
		}
	},
	onLoad: function (option) {
		console.log(option)
		this.title=option.title;
		console.log(this.title)
		console.log(option.title)
		uni.setNavigationBarTitle({
			 title: `添加${this.title}`
		})
	},
	methods:{
		postAccount(){
			let params;
			switch (this.title){
				case 'Facebook':
					params={
						id:common.userId,
						F:this.account
					}
					break;
				case 'Twitte':
					params={
						id:common.userId,
						T:this.account
					}
					break;
				case 'Instagram':
					params={
						id:common.userId,
						I:this.account
					}
					break;
				case 'Line':
					params={
						id:common.userId,
						L:this.account
					}
					break;
				case 'WeChat':
					params={
						id:common.userId,
						W:this.account
					}
					break;
				default:
					break;
			}
			const http = new Request();
			console.log(params);
			http.post('/user', {},{
				params:params,
				header: {
				  'Content-Type': 'application/json;charset=UTF-8'
				},
				dataType: 'json',
				responseType: 'text'
			}).then(res => {
				console.log(res);
				if(res.data.result.result==1){
					uni.showToast({
						title:'修改成功'
					})
					setTimeout(()=>{
						uni.navigateBack({
							delta:1
						})
					},1500)
				}else{
					uni.showToast({
						title:'请重试'
					})
				}
			}).catch(err =>{
				console.log(err);
			})
		}
		
	}
}
